import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Message, ConfirmationService } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import * as $ from 'jquery';
import * as moment from 'moment';
import * as _ from 'lodash';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { MarketingHttpService } from '../../http/marketing.http.service';


/**
 * 
 */
@Component({
	selector: 'client-list',
	templateUrl: './client-list.component.html',
	styleUrls: ['./client-list.component.scss'],
	providers: [MarketingHttpService,ConfirmationService]
})
export class ClientListComponent implements OnInit {
	detailDisplay: boolean = false;
	updateDisplay: boolean = false;
	tableHeader: any = [];
	dataList: any = [];
	totalNum: number;
	msgs: any[];
	inValueDetail:any;//
	inValueUpdate:any;//
	constructor(
		public marketingHttpService: MarketingHttpService,
		public confirmationService:ConfirmationService
	) {

	}

	ngOnInit() {
		this.queryClick()

	}
	//
	queryClick() {
		this.marketingHttpService.queryCustomerByTopicId({}).subscribe(data => {//
			if (data.returnCode.code == 'success') {
				this.tableHeader = data.fieldMap;//
				this.dataList = data.customerList;//
				this.totalNum = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}
	//
	toDelete(item) {
		this.confirmationService.confirm({
            message: '?',
            accept: () => {
                this.marketingHttpService.delSelectCustomer({}).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                })
            }
        });
	}
	//
	toDetail(item) {
		this.detailDisplay=true;
		this.inValueDetail=item;
	}
	//
	toRevamp(item) {
		this.updateDisplay=true;
		this.inValueUpdate=item;
	}
	//
	outValueUpdate (event) {
		this.updateDisplay=event;
		this.queryClick();
	}

}
