import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { Message, ConfirmationService } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { MarketingHttpService } from '../../http/marketing.http.service';

@Component({
	selector: 'marketing-activity-detail',
	templateUrl: './marketing-activity-detail.component.html',
	styleUrls: ['./marketing-activity-detail.component.scss'],
	providers: [Commfunc, CusOperationHttpService, ConfirmationService, MarketingHttpService]
})
export class MarketingActivityDetail implements OnInit {
	showFlag: boolean = true;
	name: string;
	UPLOAD: string = TZB_CUS_UPLOAD;//
	DOWNLOAD: string = TZB_CUS_DOWNLOAD;//
	fileName: string;
	filePath: string;
	fileList: any = [];
	files: any = [];
	msgs: Message[] = [];
	fileList1: any = [];//
	fileList2: any = []; //
	tabMesg: string = tableMessage;
	marketingContentFiles: any = [];
	openActive: string = 'open';
	aa: any = [1, 2];
	productData: any = [];//
	channel: any = [];//
	promote: any = [];//
	requestList: any = [];//
	display: boolean = false;
	headerTitle: string;
	marketingCampaignId: string;
	// inValue:any;
	totalMessage: any = {};//
	code: any;
	seqFlag: any = [];
	seqFlag1: any = [];
	MarketingWay: any = [];
	PromoteType: any = [];
	ConditionType: any = [];
	templateList: any = [];
	inValue: any;
	undisplay: boolean = false;
	copyDisplay: boolean = false;
	userId: string;
	typeCenter: string;
	releaseUserId: string;
	copyData: any = {};
	marketingProgramId: string;
	status: any = [];
	positionList: any = [];
	passiveChannelList: any = [];//
	channelList: any = [];
	raskRule: any = [];
	PassiveChannelId: any = [];
	PassiveChannelIds: any = [];
	pushObj: any = [];
	dataListPro: any = [];
	sellListPro: any = [];
	detailTable: any;
	ch: any = CALENDAR_CN;
	MarketingCampaigntType: any = [];
	type: string;
	flagSumit: boolean = false;
	url: any = [];
	BasicElement: boolean = false;
	MarketingObj: boolean = false;
	ReleProduct: boolean = false;
	MarketingTool: boolean = false;
	MarketingChannel: boolean = false;
	MarketingRequire: boolean = false;
	paramObj: any = {};
	dealPerShow: boolean = false;//
	waitSelUserList: any;
	outVal: any;
	campaign_approveSkipFlag: string;
	postId: string;
	orgId: string;
	campaign_FHapproveSkipFlag: string;
	marketingCampaigntType: string;//
	dataMap: any = {};
	deId: string;
	batchId: any;
	secBatchId: any;
	dataSource: any;
	pushStatOptions: any = [];//
	marketStatOptions: any = [];//
	arrList: any = [];//
	is: any = [];
	pageSize: number;
	pageNum: number;
	first: number;
	pushTime: any;
	limitOrg: any;
	total: number = 0;
	constructor(
		public activatedRoute: ActivatedRoute,
		public cusOperationHttpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public router: Router,
		public confirmationService: ConfirmationService,
		public marketingHttpService: MarketingHttpService,

	) {
		if (this.activatedRoute.snapshot.params['marketingCampaignId']) {
			this.marketingCampaignId = this.activatedRoute.snapshot.params['marketingCampaignId'];
			this.marketingCampaigntType = this.activatedRoute.snapshot.params['marketingCampaigntType'];
			this.batchId = this.activatedRoute.snapshot.params['batchId'];
			this.secBatchId = this.activatedRoute.snapshot.params['secBatchId'];
			this.dataSource = this.activatedRoute.snapshot.params['dataSource'];
		} else {
			this.dataMap = JSON.parse(this.activatedRoute.snapshot.params.dataMap);
			this.marketingCampaignId = this.dataMap.bizPkFieldMap.marketingCampaignId;
			this.marketingCampaigntType = this.dataMap.bizPkFieldMap.marketingCampaigntType;
			console.log(this.dataMap);
		}

		// this.deId = this.commfunc.getSessionDataCH('deId');
		console.log(this.commfunc.getSessionDataCH('deId'))

		this.type = this.activatedRoute.snapshot.params['type'];//
		let chName = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		this.userId = chName.userId;
		this.typeCenter = this.activatedRoute.snapshot.params['statusId'];
		this.releaseUserId = this.activatedRoute.snapshot.params['releaseUserId'];
		this.campaign_approveSkipFlag = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.campaign_approveSkipFlag;//
		this.campaign_FHapproveSkipFlag = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.campaign_FHapproveSkipFlag;//
		this.postId = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.postId;
		this.copyData = this.activatedRoute.snapshot.params;
		if (this.userId == this.releaseUserId && this.typeCenter == '3') {
			this.undisplay = true;
		} else {
			this.undisplay = false;
		}
		if (this.typeCenter == '3' || this.typeCenter == '8' || this.typeCenter == '9') {
			this.copyDisplay = true;
		} else {
			this.copyDisplay = false;
		}
		this.codeValues() //
		//,
		this.seqFlag = this.code['SEQFlAG'];//
		this.seqFlag1 = this.code['MarkingSeqFlag'];//
		this.status = this.code['MarketingStatus'];
		this.MarketingWay = this.code['MARKETINGWAY'];//
		this.PromoteType = this.code['PROMOTETYPE'];//
		this.ConditionType = this.code['CONDITETYPE'];//
		this.PassiveChannelId = this.code['PassiveChannelId'];//
		this.PassiveChannelIds = this.code['PassiveChannelIds'];//
		this.raskRule = this.code['RASKRULE'];//
		this.pushObj = this.code['pushObj'];//
		this.MarketingCampaigntType = this.code['MarketingCampaigntType'];//

		if (this.type == 'marking') {//
			this.checkSerIal();
			this.flagSumit = true;
		}
		this.url = JSON.parse(this.commfunc.getSessionDataCH('markingURL'));//
	}

	ngOnInit() {
		this.pushTime = JSON.parse(this.commfunc.getSessionData('commonHeader')).lastTimeZone;
		this.pushStatOptions = this.code['Push_Stat'];//
		this.marketStatOptions = this.code['Market_Stat'];//
		this.is = this.code['VastRejList'];//
		if (this.marketingCampaigntType == '12') {
			this.chooseBtn(this.batchId, this.dataSource);
			this.query();
			this.pageSize = 10;
			this.pageNum = 1;
			this.first = 0;
			this.queryRecommendProduct();
		}
		if (this.activatedRoute.snapshot.params['taskId']) {
			this.showFlag = false;//
		} else {
			this.showFlag = true;
		}
		this.tointialize();//
		this.commfunc.clearSession('flagIndex');
	}
	//
	chooseBtn(batchId, dataSource) {
		let params = {
			// batchId: batchId,//
			// dataSource: dataSource,//
			marketingCampaignId: this.marketingCampaignId,
		}
		this.cusOperationHttpService.queryTableHeader(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.arrList = data.tableHeaderList;
			} else {
				this.msgs = [];
				data.returnCode.message ? data.returnCode.message : '';
				this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
			}
		}, err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '', detail: '' }];
		})
	}
	//
	query() {
		let params = {
			// batchId: this.batchId,//
			// dataSource: this.dataSource,//
			// secBatchId: this.secBatchId,
			marketingCampaignId: this.marketingCampaignId,
			pageNum: this.pageNum,	//
			pageSize: this.pageSize,	//
		}
		this.cusOperationHttpService.queryCampaignCust(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.detailTable = data.campaignCustList;
				this.total = data.totalNum;
			} else {
				this.msgs = [];
				data.returnCode.message ? data.returnCode.message : '';
				this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
			}
		}, err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '', detail: '' }];
		})
	}
	//
	paginate(event) {
		this.pageSize = event.rows * 1;
		//
		this.pageNum = event.page + 1;
		this.first = event.page * this.pageSize;
		this.query();
	}
	// --
	queryRecommendProduct() {
		let param = {
			// batchId: this.batchId,// 
			marketingCampaignId: this.marketingCampaignId,// 
		}
		this.marketingHttpService.queryRecommendProduct(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.sellListPro = data.recommendProductList;
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}
	//
	tointialize() {
		let param = {
			marketingCampaignId: this.marketingCampaignId
		}
		this.cusOperationHttpService.queryMarketingCampaignById(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.totalMessage = data;
				console.log(this.totalMessage);
				this.deId = data.deId;
				this.marketingProgramId = data.marketingProgramId;
				this.marketingCampaigntType = data.marketingCampaigntType;
				this.fileList1 = data.marketingContentFileList;//
				this.fileList2 = data.marketingTargetFileList;//
				this.dataListPro = data.relationProductList;//
				this.productData = data.prdAndPromRelList;//
				this.passiveChannelList = data.passiveChannelList;//
				this.positionList = data.positionList;//
				this.channelList = data.channelList;
				this.requestList = data.requireList;//
				if (this.totalMessage.marketingCampaignMap == null) {
					this.totalMessage.marketingCampaignMap = {}
				} else if (this.totalMessage.marketingCampaignMap.limitOrg && this.totalMessage.marketingCampaignMap.limitOrg != null) {
					this.cusOperationHttpService.queryOrgById({ orgId: this.totalMessage.marketingCampaignMap.limitOrg }).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.limitOrg = data.orgName
						}
						else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
						}
					}, err => {
						this.msgs = [];
						this.msgs = [{ severity: 'error', summary: '', detail: '' }];
					});
				}
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}


	//
	terminate() {
		this.headerTitle = "";
		this.display = true;
		this.inValue = { marketingCampaignId: this.marketingCampaignId }
	}
	//
	outValue(param) {
		this.display = param;
	}
	//
	copy() {
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/create-marketing-activite/basic-element', this.copyData], { queryParams: { flag: '1' } });

	}
	//
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code
	}
	//
	btnDetail() {
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-project/marketing-program-detail', { marketingProgramId: this.marketingProgramId }])
	}

	//
	returnBtn() {
		// setTimeout(() => {
		// 	window.history.go(-2);
		// }, 500)
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/marketing-activity-query'])
	}
	//
	down(temp) {
		let name = temp.fileName;
		let path = temp.fileUrl;
		open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
		console.log(encodeURI(name))
		// let name = '';
		// let path = temp.fileUrl;
		// let u_agent = navigator.userAgent;
		// if (u_agent.indexOf('Chrome') > -1) {
		// 	let donwname = temp.fileName.replace('(', '');
		// 	name = donwname.replace(')', '');
		// 	open(TZB_CUS_DOWNLOAD + "?fileName=" + name + "&fileUrl=" + path);
		// } else {
		// 	name = temp.fileName;
		// 	open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);

	}
	//
	subMit() {
		if (this.marketingCampaigntType == '13' || this.marketingCampaigntType == '16') {
			debugger;
			if (this.campaign_approveSkipFlag) {
				this.confirmationService.confirm({
					message: "'',?",
					accept: () => {
						let arr = {};
						let campaign_approveObj = {};
						arr['marketingCampaignId'] = this.marketingCampaignId;
						arr['deId'] = this.deId;
						arr['marketingCampaigntType'] = this.marketingCampaigntType;
						campaign_approveObj['campaign_approveSkipFlag'] = this.campaign_approveSkipFlag,
							this.paramObj = {
								bizType: '02',
								bizPkFieldMap: arr,
								bizFieldMap: campaign_approveObj,
							}
						this.approveClick(this.paramObj);
					},
					reject: () => {//
						let arr = {};
						let campaign_approveObj = {};
						arr['marketingCampaignId'] = this.marketingCampaignId;
						arr['deId'] = this.deId;
						arr['marketingCampaigntType'] = this.marketingCampaigntType;
						this.paramObj = {
							bizType: '02',
							bizPkFieldMap: arr,
						}
						let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
						if (s.extParam.specifyNextOpers) {
							delete s.extParam.specifyNextOpers;
							let commonHeader = s;
							this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
						}
						this.waitSelUserList = this.commfunc.query('YXHDFHLC', { postId: this.postId, marketingCampaigntType: this.marketingCampaigntType });
						if (!this.waitSelUserList.erro) {
							this.outVal = this.waitSelUserList.waitSelUserList;
							let chooseThis = this.waitSelUserList.display;
							console.log(this.dealPerShow);
							let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
							let a2 = commonHeader.extParam.specifyNextOpers;
							console.log(a2);
							if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
								this.dealPerShow = true;
							} else {
								this.approveClick(this.paramObj);
							}
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
						}
					}
				});
			} else if (this.campaign_FHapproveSkipFlag == 'BELONG_MODE') {
				this.confirmationService.confirm({
					message: "'',?",
					accept: () => {
						let arr = {};
						let permissionModeObj = {};
						arr['marketingCampaignId'] = this.marketingCampaignId;
						arr['deId'] = this.deId;
						arr['marketingCampaigntType'] = this.marketingCampaigntType;
						permissionModeObj['campaign_FHapproveSkipFlag'] = this.campaign_FHapproveSkipFlag,
							this.paramObj = {
								bizType: '02',
								bizPkFieldMap: arr,
								bizFieldMap: permissionModeObj,
							}
						this.approveClick(this.paramObj);
					},
					reject: () => {//
						let arr = {};
						let campaign_approveObj = {};
						arr['marketingCampaignId'] = this.marketingCampaignId;
						this.paramObj = {
							bizType: '02',
							bizPkFieldMap: arr,
						}
						let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
						if (s.extParam.specifyNextOpers) {
							delete s.extParam.specifyNextOpers;
							let commonHeader = s;
							this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
						}
						this.waitSelUserList = this.commfunc.query('YXHDFHLC', { postId: this.postId, marketingCampaigntType: this.marketingCampaigntType });
						if (!this.waitSelUserList.erro) {
							this.outVal = this.waitSelUserList.waitSelUserList;
							let chooseThis = this.waitSelUserList.display;
							console.log(this.dealPerShow);
							let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
							let a2 = commonHeader.extParam.specifyNextOpers;
							console.log(a2);
							if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
								this.dealPerShow = true;
							} else {
								this.approveClick(this.paramObj);
							}
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
						}
					}
				});
			} else {
				this.confirmationService.confirm({
					message: '?',
					accept: () => {
						let arr = {};
						let campaign_approveObj = {};
						arr['marketingCampaignId'] = this.marketingCampaignId;
						arr['deId'] = this.deId;
						arr['marketingCampaigntType'] = this.marketingCampaigntType;
						this.paramObj = {
							bizType: '02',
							bizPkFieldMap: arr,
						}
						let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
						if (s.extParam.specifyNextOpers) {
							delete s.extParam.specifyNextOpers;
							let commonHeader = s;
							this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
						}
						this.waitSelUserList = this.commfunc.query('YXHDFHLC', { postId: this.postId, marketingCampaigntType: this.marketingCampaigntType });
						if (!this.waitSelUserList.erro) {
							this.outVal = this.waitSelUserList.waitSelUserList;
							let chooseThis = this.waitSelUserList.display;
							console.log(this.dealPerShow);
							let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
							let a2 = commonHeader.extParam.specifyNextOpers;
							console.log(a2);
							if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
								this.dealPerShow = true;
							} else {
								this.approveClick(this.paramObj);
							}

						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
						}
					}
				});
			}
		} else {//
			if (this.marketingCampaigntType == '12') {
				// 
				let params = {
					batchId: this.batchId,
					secBatchId: this.secBatchId,
					sourceStat: '01',
					marketingCampaignId: this.marketingCampaignId,
				}
				this.marketingHttpService.updateBusiBatchInfo(params).subscribe(data => {
					if (data.returnCode.code == 'success') {
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: '' });
				});
				// 
				let parames = {
					batchId: this.batchId,
					secBatchId: this.secBatchId,
					marketingCampaignId: this.marketingCampaignId,
					marketStat: '02',
					pushStat: '02',
					pushTime: this.pushTime.substring(0, 8),
				}
				this.marketingHttpService.updateBusiBatchDetailInfo(parames).subscribe(data => {
					if (data.returnCode.code == 'success') {
						debugger;
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: '' });
				});
			}
			this.confirmationService.confirm({
				message: '?',
				accept: () => {
					let arr = {};
					let campaign_approveObj = {};
					arr['marketingCampaignId'] = this.marketingCampaignId;
					arr['deId'] = this.deId;
					arr['marketingCampaigntType'] = this.marketingCampaigntType;
					this.paramObj = {
						bizType: '03',
						bizPkFieldMap: arr,
					}
					this.approveClick(this.paramObj);
				}
			});
		}
	}
	//
	approveClick(param) {
		this.cusOperationHttpService.custMarketWorkflowUnifiedEntrance(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
				commonHeader['globalSeqNo'] = '';
				this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
				this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/marketing-activity-query', { marketingCampaigntType: this.marketingCampaigntType }])
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}

		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}

	outDisplayCall(event) {
		console.log(event);
		this.dealPerShow = event;
	}
	outValueCall(event) {
		console.log(event);
		this.outVal = event;
		let ss = '';
		this.outVal.forEach(item => {
			ss += item.tellerId + ','
		});
		let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		commonHeader.extParam['specifyNextOpers'] = ss;
		console.log(commonHeader);
		this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
		this.approveClick(this.paramObj);
	}

	//
	checkSerIal() {
		this.marketingHttpService.createGlobalSeqNo({}).subscribe(data => {
			if (data.returnCode.code == 'success') {
				let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
				commonHeader['globalSeqNo'] = data.globalSeqNo;
				this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}

		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}
}
